import fetch from '../fetch'

/**
 * payitem service
 */
const Api = {
  get: (id) => fetch({
    url: '/api/manage/service/get/' + id,
    method: 'get'
  }),
  list: () => fetch({
    url: '/api/manage/service/list',
    method: 'get'
  }),
  save: (data) => fetch({
    url: '/api/manage/service/save',
    method: 'post',
    data: data
  }),
};

export default Api