<template>
  <div class="tax-page apis">
    <h4>API管理</h4>
    <div class="content">
      <div class="options">
        <span>提供api服务的上线、停用、定价的功能</span>
      </div>
      <div class="api" v-for="api in apis" :key="api.id">
        <div class="-icon">{{api.storeName}}</div>
        <div class="-info">
          <div class="-ops">
            <span class="-name">{{api.name}}</span>
            <span class="-price"><span class="-p1">￥</span><span class="-p2">{{api.price}}</span><span
                class="-p3">/年</span></span>
            <swak-link :to="'/system/api/' + api.id">修改</swak-link>
          </div>
          <div class="-desc">{{api.descs}}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import SwakLink from "../../ui/components/link/link";
  import ServiceApi from '@/api/system/api';

  export default {
    components: {SwakLink},
    name: "apis",
    data() {
      return {
        apis: []
      }
    },
    mounted() {
      this.$emit('layout:load', "apis")
      this.loadApis();
    },
    methods: {
      loadApis: function () {
        ServiceApi.list().then(res => {
          this.apis = res.obj || [];
        })
      }
    }
  }
</script>

